import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { Mail, Phone, Github, Linkedin } from 'lucide-react';
import emailjs from '@emailjs/browser';

function Contact() {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    message: ''
  });

  const [statusMessage, setStatusMessage] = useState(null); // Track success or failure message

  const handleSubmit = (e) => {
    e.preventDefault();

    emailjs
      .send(
        "service_aw3gwn9", // Replace with EmailJS Service ID
        "template_1880vmo", // Replace with EmailJS Template ID
        {
          from_name: formData.name,
          from_email: formData.email,
          message: formData.message
        },
        "zFYIy93O5Wy_Ln-Fg" // Replace with EmailJS Public Key
      )
      .then(
        (response) => {
          console.log("Email sent successfully!", response.status, response.text);
          setStatusMessage({ type: "success", text: "Message sent successfully!" });
          setFormData({ name: "", email: "", message: "" }); // Reset form
        },
        (error) => {
          console.error("Email failed to send:", error);
          setStatusMessage({ type: "error", text: "Failed to send message. Please try again." });
        }
      );
  };

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  return (
    <section id="contact" className="section">
      <div className="container">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          viewport={{ once: true }}
        >
          <h2 style={{ fontSize: "2rem", fontWeight: 700, marginBottom: "2rem" }}>Contact Me</h2>
          <div className="grid grid-2" style={{ gap: "4rem" }}>
            <div>
              <div style={{ marginBottom: "2rem" }}>
                <h3 style={{ fontSize: "1.25rem", fontWeight: 600, marginBottom: "1rem" }}>Get in Touch</h3>
                <p style={{ marginBottom: "2rem" }}>
                  I'm always interested in hearing about new projects and opportunities.
                </p>
                <div style={{ display: "flex", flexDirection: "column", gap: "1rem" }}>
                  <a href="mailto:kmangineni@slu.edu" className="nav-link" style={{ display: "flex", alignItems: "center", gap: "0.5rem" }}>
                    <Mail size={20} />
                    kmangineni@slu.edu
                  </a>
                  <a href="tel:+13148146677" className="nav-link" style={{ display: "flex", alignItems: "center", gap: "0.5rem" }}>
                    <Phone size={20} />
                    +1 314-814-6677
                  </a>
                  <a href="https://github.com/rcAsironman" target="_blank" rel="noopener noreferrer" className="nav-link" style={{ display: "flex", alignItems: "center", gap: "0.5rem" }}>
                    <Github size={20} />
                    GitHub
                  </a>
                  <a href="https://www.linkedin.com/in/karthikfullstackdeveloper" target="_blank" rel="noopener noreferrer" className="nav-link" style={{ display: "flex", alignItems: "center", gap: "0.5rem" }}>
                    <Linkedin size={20} />
                    LinkedIn
                  </a>
                </div>
              </div>
            </div>
            <div>
              <form onSubmit={handleSubmit} className="card">
                <div style={{ marginBottom: "1rem" }}>
                  <label htmlFor="name" style={{ display: "block", marginBottom: "0.5rem" }}>Name</label>
                  <input
                    type="text"
                    id="name"
                    name="name"
                    value={formData.name}
                    onChange={handleChange}
                    required
                    style={{
                      width: "100%",
                      padding: "0.75rem",
                      borderRadius: "0.5rem",
                      border: "1px solid var(--border)",
                      backgroundColor: "var(--background)",
                      color: "var(--text)"
                    }}
                  />
                </div>
                <div style={{ marginBottom: "1rem" }}>
                  <label htmlFor="email" style={{ display: "block", marginBottom: "0.5rem" }}>Email</label>
                  <input
                    type="email"
                    id="email"
                    name="email"
                    value={formData.email}
                    onChange={handleChange}
                    required
                    style={{
                      width: "100%",
                      padding: "0.75rem",
                      borderRadius: "0.5rem",
                      border: "1px solid var(--border)",
                      backgroundColor: "var(--background)",
                      color: "var(--text)"
                    }}
                  />
                </div>
                <div style={{ marginBottom: "1rem" }}>
                  <label htmlFor="message" style={{ display: "block", marginBottom: "0.5rem" }}>Message</label>
                  <textarea
                    id="message"
                    name="message"
                    value={formData.message}
                    onChange={handleChange}
                    required
                    rows={5}
                    style={{
                      width: "100%",
                      padding: "0.75rem",
                      borderRadius: "0.5rem",
                      border: "1px solid var(--border)",
                      backgroundColor: "var(--background)",
                      color: "var(--text)",
                      resize: "vertical"
                    }}
                  />
                </div>
                <button type="submit" className="btn btn-primary">Send Message</button>

                {/*  Show Success or Failure Message */}
                {statusMessage && (
                  <p style={{ 
                    color: statusMessage.type === "success" ? "green" : "red", 
                    marginTop: "1rem", 
                    fontWeight: "bold" 
                  }}>
                    {statusMessage.text}
                  </p>
                )}
              </form>
            </div>
          </div>
        </motion.div>
      </div>
    </section>
  );
}

export default Contact;
